export const fetchAllMemberAction = (page, search) => {
    return async (dispatch) => {
        dispatch({
            type: REQUEST_ALL_MEMBERS,
        });
        try {
            const { data, status } = await apiBaseUrl({
                method: 'GET',
                params: {
                    page,
                    size: 10,
                    search
                },
                headers: {
                    'Content-Type': 'application/json',
                },
                data: {},
                url: `/public/api/v2/member`,
            });


            if (status === 200) {
                dispatch({
                    type: REQUEST_ALL_MEMBERS_SUCCESS,
                    payload: data,
                });
                return data.result;
            }
        } catch (error) {
            dispatch({
                type: REQUEST_ALL_MEMBERS_FAILURE,
            });
            await getError(error);
        }
    };
};

export const fetchMemberDetailAction = (id) => {
    return async (dispatch) => {
        dispatch({
            type: REQUEST_MEMBER_REGISTRATION_DETAIL,
        });
        try {
            const token = localStorage.getItem('token');
            const { data, status } = await apiBaseUrl({
                method: 'GET',
                data: {},
                headers: {
                    'Content-type': 'application/x-www-form-urlencoded',
                    Authorization: `Bearer ${token}`,
                },
                url: `/api/v2/member/${id}`,
            });

            if (data.responseData.code === 404) {
                dispatch({
                    type: REQUEST_MEMBER_REGISTRATION_DETAIL_FAILURE,
                });
                return getResponseError(data.responseData.message);
            }
            if (status === 200 && data.responseData.code === 200) {
                dispatch({
                    type: REQUEST_MEMBER_REGISTRATION_DETAIL_SUCCESS,
                    payload: data,
                });
                return data;
            }
        } catch (error) {
            dispatch({
                type: REQUEST_MEMBER_REGISTRATION_DETAIL_FAILURE,
            });
            await getError(error);
        }
    };
};

