export const fetchDownloadExcelOrderJerseyAction = (page) => {
    return async (dispatch) => {
        dispatch({ type: REQUEST_DOWNLOAD_ORDER_JERSEY });

        try {
            const token = localStorage.getItem('token');
            const response = await axios.get(`${process.env.REACT_APP_BASE_URL}/api/v2/member/download-order-jersey`, {
                responseType: 'blob',
                headers: {
                    'Content-type': 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
                    Authorization: `Bearer ${token}`,
                },
            });
            const date = new Date('yy-mm-dd hh:mm:ss')
            const fileName = `Rekap_Order_Jersey-${date}.xlsx`;
            saveAs(response.data, fileName);

            dispatch({ type: REQUEST_DOWNLOAD_ORDER_JERSEY_SUCCESS });
        } catch (error) {
            dispatch({ type: REQUEST_DOWNLOAD_ORDER_JERSEY_FAILURE });
            console.error('Error downloading the file', error);
        }
    };
};

export const fetchVerificationMemberDetail = (requestParam, id, status) => {
    let pathUrl
    if (status === 'verification') {
        pathUrl = `/api/v2/member/verification/${id}/${requestParam.parentId}/${requestParam.jerseyId}`
    } else {
        pathUrl = `/api/v2/member/update/${id}/${requestParam.parentId}/${requestParam.jerseyId}`
    }
    return async (dispatch) => {
        dispatch({
            type: REQUEST_UPDATE_MEMBER,
        });
        try {
            const token = localStorage.getItem('token');
            const { data, status } = await apiBaseUrl({
                method: 'PATCH',
                headers: {
                    'Content-Type': 'application/json',
                    Authorization: `Bearer ${token}`,
                },
                data: requestParam,
                url: pathUrl,
            });

            if (status === 200) {
                dispatch({
                    type: REQUEST_UPDATE_MEMBER_SUCCESS,
                    payload: data,
                });
                return data;
            }
        } catch (error) {
            dispatch({
                type: REQUEST_UPDATE_MEMBER_FAILURE,
            });
            await getError(error);
        }
    };
}

