// All components mapping with path for internal routes

import { lazy } from 'react'

const Dashboard = lazy(() => import('../pages/protected/Dashboard'))
const Welcome = lazy(() => import('../pages/protected/Welcome'))
const Page404 = lazy(() => import('../pages/protected/404'))
const Blank = lazy(() => import('../pages/protected/Blank'))
const Regular = lazy(() => import('../pages/protected/member/MemberRegular'))
const Registration = lazy(() => import('../pages/protected/member/Registration'))
const OrderJersey = lazy(() => import('../pages/protected/member/OrderJersey'))
const RegistrationDetail = lazy(() => import('../pages/protected/member/RegistrationDetail'))
const Event = lazy(() => import('../pages/protected/event/Event'))
const AddEvent = lazy(() => import('../pages/protected/event/AddEvent'))
const RegistrationEvent = lazy(() => import('../pages/protected/event/RegistrationEvent'))

const routes = [
    {
        path: '/dashboard',
        component: Dashboard,
    },
    {
        path: '/welcome',
        component: Welcome,
    },
    {
        path: '/member/regular',
        component: Regular,
    },
    {
        path: '/member/registration',
        component: Registration,
    },
    {
        path: '/member/registration/:id/:status',
        component: RegistrationDetail,
    },
    {
        path: '/member/regular/:id/:status',
        component: RegistrationDetail,
    },
    {
        path: '/member/order-jersey',
        component: OrderJersey,
    },
    {
        path: '/event/:status',
        component: Event,
    },
    {
        path: '/add-event/:status',
        component: AddEvent,
    },
    {
        path: '/edit-event/:status/:id',
        component: AddEvent,
    },
    {
        path: '/registration-event/:id',
        component: RegistrationEvent,
    },
    {
        path: '/404',
        component: Page404,
    },
    {
        path: '/blank',
        component: Blank,
    },
]

export default routes
