import { useDispatch } from 'react-redux'
import { CONFIRMATION_MODAL_CLOSE_TYPES } from '../../../utils/globalConstantUtil'
import { deleteLead } from '../../member/registration/leadSlice'
import { showNotification } from '../headerSlice'
import { fetchDeleteMemberAction, fetchDeleteOrderJerseyAction } from "../../../app/service/action/memberAction";
import { fetchDeleteEventAction } from '../../../app/service/action/eventAction';

function ConfirmationModalBody({ extraObject, closeModal }) {

    const dispatch = useDispatch()

    const { message, type, index } = extraObject

    const proceedWithYes = async () => {
        if (type === CONFIRMATION_MODAL_CLOSE_TYPES.LEAD_DELETE) {
            // positive response, call api or dispatch redux function
            dispatch(deleteLead({ index }))
            dispatch(showNotification({ message: "Lead Deleted!", status: 1 }))
        }

        if (type === CONFIRMATION_MODAL_CLOSE_TYPES.MEMBER_DELETE) {
            dispatch(fetchDeleteMemberAction(index)).then(response => {
                if (response.result) {
                    dispatch(showNotification({ message: "Data berhasil di hapus", status: 1 }))
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000)
                }
            })
        }

        if (type === CONFIRMATION_MODAL_CLOSE_TYPES.ORDER_JERSEY_DELETE) {
            dispatch(fetchDeleteOrderJerseyAction(index)).then(response => {
                if (response.result) {
                    dispatch(showNotification({ message: "Data berhasil di hapus", status: 1 }))
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000)
                }
            })
        }

        if (type === CONFIRMATION_MODAL_CLOSE_TYPES.DELETED_EVENT) {
            dispatch(fetchDeleteEventAction(index)).then(response => {
                if (response.result) {
                    dispatch(showNotification({ message: "Data berhasil di hapus", status: 1 }))
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000)
                }
            })
        }
        closeModal()
    }

    return (
        <>
            <p className=' text-xl mt-8 text-center'>
                {message}
            </p>

            <div className="modal-action mt-12">

                <button className="btn btn-outline   " onClick={() => closeModal()}>Cancel</button>

                <button className="btn btn-primary w-36" onClick={() => proceedWithYes()}>Yes</button>

            </div>
        </>
    )
}

export default ConfirmationModalBody
